<template>
  <div class="home">
    <div class="home-left">
      <Panel>
        <template v-slot:header>
          <router-link
            :class="{ active: $route.path === '/' && item === '全部' }"
            :to="item | transformTab"
            v-for="item in menu"
            :key="item"
            active-class="active"
            >{{ item }}</router-link
          >
        </template>
        <template v-slot:content>
          <router-view> </router-view>
        </template>
      </Panel>
    </div>
    <div class="home-right">
      <Panel>
        <template #header>
          <span>个人信息</span>
        </template>
        <template #content v-if="info && user">
          <div class="top">
            <div class="handle">
              <img :src="user.avatar_url" alt="" />
              <p>{{ user.loginname }}</p>
            </div>
            <p class="score">{{ user.score }}积分</p>
            <a class="github" href="https://github.com/"
              ><i class="iconfont icon-github-fill"></i>@{{
                user.githubUsername
              }}</a
            >
            <p class="time">
              注册时间{{ user.create_at | moment("from", "now") }}
            </p>
          </div>
        </template>
        <template #content v-else>
          <div class="quit">
            <h4>极客社区</h4>
            <el-button type="primary" size="mini">
              <router-link to="/login" class="login"
                >请登录</router-link
              ></el-button
            >
          </div>
        </template>
      </Panel>
      <Panel class="pulbish" v-if="info">
        <template #content>
          <el-button type="primary" size="small" @click="publish"
            >发布文章</el-button
          >
        </template>
      </Panel>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Panel from "../containers/Panel.vue";
export default {
  components: { Panel },
  data() {
    return {
      menu: ["全部", "精华", "分享", "问答", "招聘", "客户端测试"],
    };
  },
  created() {
    if (this.info) {
      this.getUser(this.info.loginname);
    }
  },
  computed: {
    ...mapState({
      info: (state) => state.userInfor.info,
      user: (state) => state.user.user,
    }),
  },
  methods: {
    publish() {
      this.$router.push({
        name: "publish",
      });
    },
    ...mapActions(["getUser"]),
  },
};
</script>

<style lang="less">
.home {
  display: flex;
  .home-left {
    width: 75%;
    margin-right: 30px;
    a {
      color: #1e80ff;
      margin-left: 15px;
      font-size: 14px;
      padding: 4px;
      border-radius: 3px;
    }
    .active {
      background-color: #1e80ff;
      color: #fff;
    }
  }
  .home-right {
    width: 25%;
    .top {
      .handle {
        display: flex;
        align-items: center;
        margin-bottom: 8px;
        img {
          width: 40px;
          height: 40px;
          border-radius: 3px;
        }
        p {
          color: #778087;
          font-size: 14px;
          margin-left: 8px;
        }
      }
      .handle {
        display: flex;
        align-items: center;
        margin-bottom: 8px;
        img {
          width: 40px;
          height: 40px;
          border-radius: 3px;
        }
        p {
          color: #778087;
          font-size: 14px;
          margin-left: 8px;
        }
      }
      p {
        margin: 0;
      }
      .score {
        font-size: 14px;
        margin-bottom: 10px;
      }
      .github {
        color: #778087;
        font-size: 14px;
      }
      .time {
        color: #778087;
        margin-top: 8px;
      }
    }
    .quit {
      h4 {
        font-size: 14px;
        word-break: break-word;
        margin: 0;
        font-weight: normal;
        margin-bottom: 15px;
      }
      .login {
        color: #fff;
      }
    }
    .pulbish {
      margin-top: 20px;
      height: 50px;
      overflow: hidden;
    }
  }
}
</style>
