<template>
  <div class="header">
    <div class="header-inner">
      <h1>
        <router-link to="/">
          <img src="../assets/logo.png" alt="" />
        </router-link>
      </h1>
      <div class="header-right">
        <router-link to="/">首页</router-link>
        <router-link to="/">新手入门</router-link>
        <router-link to="/">API</router-link>
        <router-link to="/">关于</router-link>
        <router-link to="/login" v-if="!info">登录</router-link>
        <a href="#" v-else @click.prevent="quite">退出</a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState({
      info: (state) => state.userInfor.info,
    }),
  },
  methods: {
    quite() {
      this.quit();
      if (this.$route.path !== "/") {
        this.$router.push("/");
      }
    },
    ...mapMutations(["quit"]),
  },
};
</script>

