import axios from "../../plugins/axios";
// 文章列表
export default {
  state: () => ({
    value: [],
  }),
  mutations: {
    getList(state, payload) {
      state.value = payload;
    },
    loadMorePoad(state, payload) {
      state.value = [...state.value, ...payload];
    },
  },
  actions: {
    async getList({ commit }, payload) {
      const { page, tab } = payload;
      const searchStr = page
        ? `/topics?tab=${tab}&page=${page}&limit=20`
        : `/topics?tab=${tab}`;
      const res = await axios.get(searchStr);
      if (page) {
        commit("loadMorePoad", res.data);
      } else {
        commit("getList", res.data);
      }
    },
  },
};
