//  登录用户信息
import axios from "../../plugins/axios";
import Vue from "vue";
export default {
  state: () => ({
    info: null,
  }),
  mutations: {
    getInfor(state, info) {
      state.info = info;
    },
    quit(state) {
      state.info = null;
      window.localStorage.removeItem("info");
      window.localStorage.removeItem("token");
    },
  },
  actions: {
    getInfor({ commit }, token) {
      return new Promise((resolve, reject) => {
        axios.post("/accesstoken", { accesstoken: token }).then(
          (res) => {
            commit("getInfor", res);
            window.localStorage.setItem("info", JSON.stringify(res));
            window.localStorage.setItem("token", token);
            resolve();
          },
          (error) => {
            console.log("失败了");
            reject();
          }
        );
      });
    },
  },
  getters: {},
};
