//  文章详情
import axios from "../../plugins/axios";
export default {
  state: () => ({
    value: null,
  }),
  mutations: {
    getDetails(state, payload) {
      state.value = payload;
    },
    // 更改点赞状态
    changeUp(state, payload) {
      state.value.replies.find((el) => el.id === payload.id).is_uped =
        payload.action;
      // 更改点赞数量
      if (payload.action) {
        state.value.replies
          .find((el) => el.id === payload.id)
          .ups.push(payload.id);
      } else {
        state.value.replies.find((el) => el.id === payload.id).ups =
          state.value.replies
            .find((el) => el.id === payload.id)
            .ups.filter((el) => el !== payload.id);
      }
    },
    changeCollect(state) {
      state.value.is_collect = !state.value.is_collect;
    },
    replay(state, payload) {
      state.value.replies.push(payload);
    },
  },
  actions: {
    // 当登录后再去请求文章详情就携带token参数
    async getDetails({ commit }, id) {
      const token = window.localStorage.getItem("token");
      const url = token ? `/topic/${id}?accesstoken=${token}` : `/topic/${id}`;
      console.log(url);
      const res = await axios.get(url);
      commit("getDetails", res.data);
    },
    // 更改点赞状态
    async changeUp({ commit }, reply_id) {
      const token = window.localStorage.getItem("token");
      const res = await axios.post(
        `/reply/${reply_id}/ups?accesstoken=${token}`
      );
      commit("changeUp", {
        id: reply_id,
        action: res.action === "up" ? true : false,
      });
    },
    // 收藏
    async collect({ commit }, id) {
      const token = window.localStorage.getItem("token");
      await axios.post(`/topic_collect/collect`, {
        topic_id: id,
        accesstoken: token,
      });
      commit("changeCollect");
    },
    // 取消收藏
    async de_collect({ commit }, id) {
      const token = window.localStorage.getItem("token");
      await axios.post(`/topic_collect/de_collect`, {
        topic_id: id,
        accesstoken: token,
      });
      commit("changeCollect");
    },
    // 评论
    async replay({ dispatch }, payload) {
      console.log(payload);
      const token = window.localStorage.getItem("token");
      const res = await axios.post(`/topic/${payload.id}/replies`, {
        content: payload.content,
        accesstoken: token,
      });
      dispatch("getDetails", payload.id);
    },
  },
  getters: {},
};
